import React, { useEffect, useState } from 'react';
import bridge from '@vkontakte/vk-bridge';
import { Panel, PanelHeader, Div, PanelHeaderBack, Cell, Button, Avatar, Group, Header} from '@vkontakte/vkui/';
import AvatarApp from '../img/AvatarApp.png'
const Settings = (props) => {
	
	const [photo, setStatePhoto] = useState()
	const [userName, setStateUserName] = useState()
	const [rating, setStateRating] = useState(0)

	useEffect(() => {
		bridge.send("VKWebAppStorageGet", { "keys": ["rating"] })
			.then(data => {
				console.log(data.keys[0].value);

				if (data.keys[0].value === '') {
					setStateRating(0)
				}
				else {
					setStateRating(data.keys[0].value)
				}
			})
		
		bridge.send("VKWebAppGetUserInfo")
		.then(data => {
			setStatePhoto(data.photo_200)
			setStateUserName(data.first_name + ' ' + data.last_name)
		})
	}, [])
	
	return (

		<Panel id={props.id} separator={false}>
			<PanelHeader left={<PanelHeaderBack onClick={() => window.history.back()} />}>
				
        </PanelHeader>
			<div className='stage'>
				<div style={{ position: 'relative', marginTop: '15%' }}>
					<Group header={
						<Header> </Header>}>

						<Cell before={<Avatar mode="app" src={AvatarApp} />} description='  04.10.2020.     197   297  .' multiline={true}>
							 1.0.0
						</Cell>
					</Group>
					<Group header={
						<Header> </Header>}>

						<Cell href='https://vk.com/club199216189' target="_blank" before={<Avatar mode="app" src={AvatarApp} />} description='Mini Apps' multiline={true}>
							Towner
						</Cell>
						<Div>
							<Button mode='outline' size='xl' onClick={() => bridge.send("VKWebAppJoinGroup", { "group_id": 199216189 })}>  </Button>
						</Div>
						
					</Group>
				</div>
			</div>
		</Panel>
	)
};

export default Settings;





