import React, { useEffect, useState } from 'react';
import bridge from '@vkontakte/vk-bridge';
import Panel from '@vkontakte/vkui/dist/components/Panel/Panel';
import PanelHeader from '@vkontakte/vkui/dist/components/PanelHeader/PanelHeader';
import Separator from '@vkontakte/vkui/dist/components/Separator/Separator';
import Div from '@vkontakte/vkui/dist/components/Div/Div';
import PanelHeaderBack from '@vkontakte/vkui/dist/components/PanelHeaderBack/PanelHeaderBack';
import Cell from '@vkontakte/vkui/dist/components/Cell/Cell';
import Button from '@vkontakte/vkui/dist/components/Button/Button';
import Avatar from '@vkontakte/vkui/dist/components/Avatar/Avatar';
import Group from '@vkontakte/vkui/dist/components/Group/Group';
import Header from '@vkontakte/vkui/dist/components/Header/Header';
import Icon28ShareOutline from '@vkontakte/icons/dist/28/share_outline';

const Rating = (props) => {

	const [photo, setStatePhoto] = useState()
	const [userName, setStateUserName] = useState()
	const [rating, setStateRating] = useState(0)

	useEffect(() => {
		bridge.send("VKWebAppStorageGet", { "keys": ["rating"] })
			.then(data => {
				console.log(data.keys[0].value);

				if (data.keys[0].value === '') {
					setStateRating(0)
				}
				else {
					setStateRating(data.keys[0].value)
				}
			})

		bridge.send("VKWebAppGetUserInfo")
			.then(data => {
				setStatePhoto(data.photo_200)
				setStateUserName(data.first_name + ' ' + data.last_name)
			})

	}, [])


	return (

		<Panel id={props.id} separator={false}>
			<PanelHeader left={<PanelHeaderBack onClick={() => window.history.back()} />}>
				
        </PanelHeader>
			<div className='stage'>
				<div style={{ position: 'relative', marginTop: '15%' }}>
					<Group header={
						<Header> </Header>}>

						<Cell before={<Avatar mode="app" src={photo} />} description={': ' + `${rating}` + ' '} multiline={true}>
							{userName}
						</Cell>
						<Separator/>
						<Div>
							<Button mode='outline' before={<Icon28ShareOutline style={{ color: "white" }} />} size='xl' onClick={() => bridge.send("VKWebAppShare", { "link": "https://vk.com/app7612376" })}>  </Button>
						</Div>
					</Group>
				</div>
			</div>
		</Panel>
	)
};

export default Rating;





