import React, { useEffect } from 'react';
import bridge from '@vkontakte/vk-bridge';
import Panel from '@vkontakte/vkui/dist/components/Panel/Panel';
import Gallery from '@vkontakte/vkui/dist/components/Gallery/Gallery';
import Div from '@vkontakte/vkui/dist/components/Div/Div';
import Button from '@vkontakte/vkui/dist/components/Button/Button';
import './Style.css';
import Arrow from '../img/arrow.svg';
import Icon28CompassOutline from '@vkontakte/icons/dist/28/compass_outline';

const Intro = props => {

    


    
const firstOpenApp = () => {
    bridge.send("VKWebAppStorageSet", { "key": "hasSeenIn", "value": 'true' });
    props.goToPage('Home')
}

    return (

        <Panel id={props.id} separator={false}>
            <div className="stage">
                <Gallery 
                    slideWidth="100%"
                    style={{ height: "100%" }}
                    bullets="dark"
                >
                    <div style={{ position: 'relative', marginTop: '13%' }}>
                        <Div style={{ textAlign: 'center', fontSize: 26, fontFamily: 'Neucha' }}><h1>       Towner!</h1>
                        <img className="scale" src={Arrow} alt="" /></Div></div>
                    <div style={{ position: 'relative', marginTop: '13%' }}>
                        <Div style={{ textAlign: 'center', fontSize: 21, fontFamily: 'Neucha' }}><h1>
                              ,           !
                        </h1><img className="scale" src={Arrow} alt="" /></Div></div>
                   
                    <div style={{ position: 'relative', marginTop: '13%' }}>
                        <Div style={{ textAlign: 'center', fontSize: 19, fontFamily: 'Neucha' }}>
                            <h1>  :     /       !</h1><img className="scale" src={Arrow} alt="" /></Div></div>
                   
                    <div style={{ position: 'relative', marginTop: '72%' }}>
                        <Div >
                            <Button onClick={()=>firstOpenApp()} before={<Icon28CompassOutline width={50} height={50} style={{ color: "white" }} />} mode='outline' size='xl'> <h1 style={{ textAlign: 'center', fontSize: 40, fontFamily: 'Neucha' }}>!</h1></Button>
                        </Div></div>

                </Gallery>
            </div>
        </Panel>
    )
};

export default Intro;